.navBar {
    text-decoration: none;
    list-style-type: none;
    display: flex;
    gap: 3rem;
    justify-content: space-around;
    align-items: center;
    font-size: 19px;
}

.navBarFull {
    position: fixed;
    z-index: 1000000;
    top: 0;
    background-color: white;
    width: 100%;
    border-bottom: 0.1px solid gray;
}

.navBar li {
    display: inline-block;
    position: relative;
    cursor: pointer;
}

.navBar li:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 5px;
    bottom: 0;
    left: 0;
    border-radius: 40px;
    top: 2rem;
    background-color: #3c4043;
    opacity: 0.4;
    transform-origin: bottom right;
    transition: transform 0.4s ease-out;
  }

  .navBar li:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
.navBar li:hover {
    opacity: 0.7;
}

.fullNavBar {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
}

.fullLogo img {
    width: 300px;
}

.fullLogo img:hover {
    opacity: 0.8;
    cursor: pointer;
}

.fullLogo {
    height: 119px;
}

.posSoft {
    background-color: orange;
    padding: 0.4rem 1.1rem;
    border-radius: 28px;
}

.gridBar {
    display: grid;
    justify-items: end;
}

.navBarUpperside *{
    margin-left: 3rem;
}

.navBarUpperside a {
    color: black;
    text-decoration: none;
    font-family: roboto-regular;
    font-size: 20px;
}

.navBarUpperside button{
    background-color: #b4e3af;
    border: none;
    border-radius: 30px;
    padding: 1rem;
    margin-top: 1rem;
    font-size: 18px;
    cursor: pointer;

}

.navBarUpperside button:hover{
    text-decoration: underline;
    text-underline-offset: 0.5rem;
    text-decoration-color: rgba(128, 128, 128, 0.5);
}
