.gridBlock {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    margin-bottom: 8rem;
    }
    
    .gridBlock1 { 
        grid-area: 1 / 1 / 2 / 2;
        background-color: #edeae7;
        /* height: 300px; */
        margin: 0 0rem 0 8rem;
        border-radius: 33px;
    }
    .gridBlock2 {
        grid-area: 2 / 2 / 3 / 3;
        background-color: #edeae7;
        /* height: 260px; */
        padding: 5rem;
        margin: 0 8rem 0rem 0rem;
        border-radius: 33px;
    }