.mainFooter {
    display: flex;
    justify-content: flex-start;
    gap: 12rem;
    padding: 0 10rem 5rem 10rem;
    border-bottom: 1px solid rgba(128, 128, 128, 0.212);
}

.footer {
    background-color: #a4a8ad70;
    padding: 3rem 0 1rem 0;
    border-radius: 33px;
    margin: 1rem;
}

.socialIcons {
    color: black;
    text-decoration: none;
    font-size: 40px;
}

.flexBox{
    display: flex;
    gap: 1rem;
    margin-left: 1rem;
    margin-top: 1rem;
}