.container {
        position: relative;
        text-align: center;
        margin-top: 13rem !important;
        background-image: url('../images/introductionTop.png');
        background-repeat: no-repeat;
        background-size: 30%;
        background-position-x: 107%;
}

.container .posimage {
    width: 60%;
    top: 10%;
    left: 21%;
    position: relative;
    animation: alternate;
    animation-name: run;
    animation-duration: 1.5s;
}

  @keyframes run {

    0% {
      left: 100%;
    }
    50% {
      left: 10%;    
    }
    75% {
        left: 27%;
    }
    100% {
        left: 21%;
    }
  }

.top-left {
    position: absolute;
    top: 70px;
    left: 4%;
    margin: 1rem;
    font-family: roboto-black;
    font-size: 70px;
    display: grid;
    justify-items: start;
    text-shadow: 1px 1px;
    color: #3c4043;
    z-index: 1000;
}

.introBottom {
    position: absolute;
    bottom: 17px;
    left: -17px;
    z-index: 1;
}